import { Link } from "gatsby";
import React from "react";
import bottomImg1 from "../../assets/images/product/Rectangle 2.png";
import bottomImg2 from "../../assets/images/product/Frame 14.svg";
import bottomImg3 from "../../assets/images/product/Frame (27).svg";

import productCateg1 from "../../assets/images/product/Capture-3.png";

import productCateg3 from "../../assets/images/product/Capture-5.png";
import productCateg4 from "../../assets/images/product/Capture-6.png";
import shade1 from "../../assets/images/product/shade1.jpg";
import shade2 from "../../assets/images/product/shade2.jpg";
import shade3 from "../../assets/images/product/shade3.jpg";
import shade4 from "../../assets/images/product/shade4.jpg";
import "./product.css";

const ProductDetails = (product) => {
  let data = product?.product;
  console.log(data, "name");
  return (
    <div className='productdetail-section home-content'>
      <div className='header-title'>
        <div className='container'>
          <div className='header-title-text'>
            <h2>
              EXPLORE <span>OUR TILE RANGE</span>
            </h2>
            <ul>
              <li>
                <Link to='/'>Home</Link>
              </li>
              <li>/ Products</li>
              <li>/ Architectural Concrete</li>
            </ul>
          </div>
        </div>
      </div>

      <div className='productdetail-wrapper'>
        <div className='productdetail-left'>
          <div className='mainImgContainer'>
            <img src={productCateg1} alt='' />
          </div>
          <div className='otherShadesTypeImg'>
            <img src={shade1} alt='' />
            {/* <img src={require(`../../assets/images/product/${ product.img }`).default} alt='' /> */}

            <img src={shade2} alt='' />
            <img src={shade3} alt='' />
            <img src={shade4} alt='' />
          </div>
        </div>

        <div className='productdetail-right'>
          <div className='product-right-wrapper'>
            <div className='productdetail-title'>
              <h5>{data?.id}</h5>
              {/* <span>$ 120.00</span> */}
            </div>
            <div className='productdetail-desc'>
              <div className='productdeatails-subtitle'>
                <h6>Series</h6>
                <span>:</span>
                <span>{data?.series}</span>
              </div>
              <div className='productdeatails-subtitle'>
                <h6>Code</h6>
                <span>:</span>
                <span>{data?.code}</span>
              </div>
              <div className='productdeatails-subtitle'>
                <h6>Description</h6>
                <span>:</span>
                <span>{data?.description}</span>
              </div>
              <div className='productdeatails-subtitle'>
                <h6>Design Faces</h6>
                <span>:</span>
                <span> {data?.designFaces}</span>
              </div>
              <div className='productdeatails-subtitle'>
                <h6>Slip Rating</h6>
                <span>:</span>
                <span>{data?.slipRating}</span>
              </div>
              <div className='productdeatails-subtitle'>
                <h6>Finish</h6>
                <span>:</span>
                <span>{data?.finish}</span>
              </div>
              <div className='productdeatails-subtitle'>
                <h6>Edge</h6>
                <span>:</span>
                <span>{data?.edge}</span>
              </div>
              <div className='productdeatails-subtitle'>
                <h6>Area</h6>
                <span>:</span>
                <span>{data?.area}</span>
              </div>
            </div>
            <div className='productdetail-color'>
              <h6>Color</h6>
              <div className='color-catalog'>
                <span>
                  <img
                    src='https://gotilesqld.com/wp-content/uploads/2020/11/front-pic-4.jpg'
                    alt='title catalog'
                  />
                </span>
                <span>
                  <img src={productCateg4} alt='title catalog' />
                </span>
                <span>
                  <img src={productCateg3} alt='title catalog' />
                </span>
              </div>
            </div>
            <Link to='/contactUs'>Enquire Now</Link>
          </div>
        </div>
      </div>
      <div className='bgDetailsbottom'>
        <div className='container'>
          <div className='productDetailPageFooter'>
            <div className='detailFooter'>
              <h2>
                Let’s Be in <span>Touch</span>
              </h2>
              <p>
                Flex is a Small SaaS Business. Flex isn’t a traditional company.
                We believe a diverse team, approaches to work and transparency
                are key to our success.
              </p>
            </div>
            <div className='detailFooterImg'>
              <img src={bottomImg2} alt='' className='blogImg' />
              <img src={bottomImg1} alt='' className='mainDetailImg' />
              <img src={bottomImg3} alt='' className='dotRectangleImg' />
            </div>
          </div>
        </div>
      </div>
      <div className='product-look-section home-contact'>
        <div className='home-contact-grid'>
          <h2>
            Have a <span> Look</span>
          </h2>
        </div>
      </div>
    </div>
  );
};

export default ProductDetails;
